<!-- 컴포넌트 ui 정의 -->
<!-- root element가 하나만 있어야함 -->
<template>
  <div>
    <div class="text-center justify-center py-3">
      <span class="font-weight-bold text-h5 navy--text">이벤트</span>
    </div>
    <v-tabs v-model="eventTab" slider-color="beige" slider-size="3">
      <v-tab active-class="navy beige--text" class="rounded-tr-xl grow black--text text-no-wrap" width="50%" >진행 중인 이벤트</v-tab>
      <v-tab active-class="navy beige--text" class="rounded-tr-xl grow black--text text-no-wrap" width="50%" >종료된 이벤트</v-tab>
    </v-tabs>
    <div v-show="eventTab===0">
      <event-card v-for="coupon in coupons" :key="coupon.img" class="mt-5">
        <template v-slot:title>
          <div class="font-weight-high">{{ coupon.title }}</div>
        </template>
        <template v-slot:image>
          <router-link :to="`/event/detail?name=${coupon.coupon_no}&hit=true`">
            <v-img class="rounded-lg" :src="coupon.img" object-fit:contain> </v-img>
          </router-link>
        </template>
        <template v-slot:content> {{ coupon.content }} 구매시 할인 적용 </template>
      </event-card>
    </div>
      <div v-show="eventTab===1">
        <event-card v-for="ecoupon in ExpiredCoupons" :key="ecoupon.img">
        <template v-slot:title>
          <div class="font-weight-high">{{ ecoupon.title }}</div>
        </template>
        <template v-slot:image>
          <router-link :to="`/event/detail?name=${ecoupon.coupon_no}&hit=true`">
            <v-img class="rounded-lg" :src="ecoupon.img" object-fit:contain> </v-img>
          </router-link>
        </template>
        <template v-slot:content> {{ ecoupon.content }} 구매시 할인 적용 </template>
        </event-card>
      </div>
    <div>

      <!-- 기본적으로 버튼과 버튼 사이에 마진이 들어감 -->
    </div>
  </div>
</template>

<script>
import apiCoupon from "@/apis/coupon";
import EventCard from "@/components/event/EventCard";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "Event",
  // 추가하고 싶은 컴포넌트를 등록
  components: {
    EventCard,
  },

  //컴포넌트에서 사용하는 데이터를 정의
  // return 값이 되려면 괄호로 감싸야함
  data() {
    return {
      coupons: [],
      ExpiredCoupons: [],
      eventTab: null,
    };
  },
  //컴포넌트에서 사용하는 메소드 정의
  methods: {
    getCoupon() {
      console.log("getcoupon 실행");
      apiCoupon
        .getCouponList()
        .then((response) => {
          this.coupons = response.data;
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    },
    getExpiredCoupon() {
      apiCoupon
        .getExpiredCouponList()
        .then((response) => {
          this.ExpiredCoupons = response.data;
          console.log(response.data);
        })
        .catch((error) => {
          console.log(error);
        });
    }
  },
  created() {
    console.log("실행");
    this.getCoupon();
    this.getExpiredCoupon();
  },
};
</script>
<!-- 컴포넌트 -->
<style scoped></style>
