<!-- 컴포넌트 ui 정의 -->
<!-- root element가 하나만 있어야함 -->
<template>
  <div>
    <v-card-title class="text-center justify-center py-3">
      <span class="font-weight-bold text-h5 navy--text">{{ couponTitle }}</span>
    </v-card-title>
    <v-card>
      
      <v-img :src="`${couponImg}`" contain> </v-img>

      <v-col class="text-center">
        <v-btn block class="navy white--text" outlined @click="issueCoupon" :disabled="isIssued">
          쿠폰 발급
        </v-btn>
      </v-col>
      <!-- 기본적으로 버튼과 버튼 사이에 마진이 들어감 -->
    </v-card>
  </div>
</template>

<script>
import VueCookie from "vue-cookies";
import eventApi from "@/apis/coupon";
export default {
  // 컴포넌트의 대표이름(devtools에 나오는 이름)
  name: "eventpageDetail",
  // 추가하고 싶은 컴포넌트를 등록
  components: {},
  //컴포넌트에서 사용하는 데이터를 정의
  // return 값이 되려면 괄호로 감싸야함
  data() {
    return {
      isIssued: false,
      couponName: "",
      couponImg: "",
      couponNo: "",
      couponTitle: null,
    };
  },
  //컴포넌트에서 사용하는 메소드 정의
  methods: {
    getCouponName() {
      this.couponName = this.$route.params.couponName;
      console.log("here");
      console.log(this.couponName);
    },
    issueCoupon() {
      eventApi
        .issueCoupon(this.couponNo)
        .then((response) => {
          console.log(response.data);
          if (response.data.result == "success") {
            this.isIssued = true;
            alert("발급 성공!");
          }
        })
        .catch((error) => {
          alert("발급 실패하였습니다");
          console.log(error);
        });
    },
  },
  created() {
    console.log(this.$route);
    let hit = this.$route.query.hit;
    console.log(hit);
    if (VueCookie.isKey("hit_" + this.$route.query.name)) {
      hit = false;
    } else {
      VueCookie.set("hit_" + this.$route.query.name, eventApi.getIP());
    }
    console.log("hit : " + hit);
    eventApi
      .readEvent(this.$route.query.name, hit)
      .then((response) => {
        console.log(this.$route.query.name);
        console.log(response.data);
        this.couponName = response.data.title;
        this.couponImg = response.data.img;
        this.couponTitle = response.data.title;
        this.couponNo = response.data.coupon_no;
        console.log(this.couponNo);
      })
      .catch((error) => {
        console.log(this.$route.query.name);
        if (error.response) {
          if (error.response.status == 403) {
            this.$router.push("/login");
          }
        }
      });
    //
  },
  mounted() {},

  // 컴포넌트가 처음 렌더링 될 때 실행
  beforeCreate() {
    console.log("beforeCreate");
  },

  // 컴포넌트가 업데이트 될 때 실행
  beforeUpdate() {
    console.log("beforeUpdate");
  },
  updated() {
    console.log("updated");
    eventApi.isIssued(this.couponNo).then((response) => {
      console.log("here : " + response.data);
      console.log(response.data);
      this.isIssued = response.data.isIssued;
    });
  },
};
</script>
<!-- 컴포넌트 -->
<style scoped></style>
